@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{text-align:center;background-color:#FAF7DE;min-height:100vh;display:flex;flex-direction:column;font-family:'IBM Plex Mono', monospace;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;height:100%}.time{font-size:3rem;padding:6rem}.timer-width{min-width:100px;padding:3em;text-align:center}.button{padding:.6rem 1.5rem;margin:.4rem;border-radius:3px;text-transform:uppercase;font-weight:600;font-size:.8rem;border-style:groove;width:7em}.button:focus{outline-width:0}.button-primary:hover{background-color:#c8d6f1;border:1px solid #c8d6f1}.button-primary:active{background-color:#c8d6f1;border:1px solid #c8d6f1}.button-primary-active{background-color:#c8d6f1;border:1px solid #c8d6f1;color:#152d4a}.button-primary-inactive{background-color:#c8d6f1;border:1px solid #c8d6f1;color:#152d4a}.timer-circle{height:225px;width:225px;background-color:#e4d098;border-radius:43% 57% 48% 52% / 47% 43% 57% 53%;line-height:225px;display:inline-block;text-align:center;vertical-align:middle;color:#152d4a;-webkit-animation:wobble 7s linear infinite;animation:wobble 7s linear infinite}.timer-circle-grow{-webkit-animation:grow 7s ease-in-out infinite, wobble 7s linear infinite;animation:grow 7s ease-in-out infinite, wobble 7s linear infinite}.circle-button{font-family:'IBM Plex Mono', monospace;height:55px;width:55px;background-color:#c8d6f1;border-radius:50%;line-height:55px;display:inline-block;text-align:center;vertical-align:middle;color:#152d4a;margin:0.2em}.circle-button:hover{background-color:#eee8fa;border:1px solid #eee8fa}@-webkit-keyframes wobble{0%{border-radius:43% 57% 48% 52% / 47% 43% 57% 53%}16%{border-radius:34% 66% 63% 37% / 63% 46% 54% 37%}33%{border-radius:58% 42% 36% 64% / 52% 70% 30% 48%}50%{border-radius:64% 36% 63% 37% / 68% 46% 54% 32%}67%{border-radius:41% 59% 36% 64% / 52% 33% 67% 48%}83%{border-radius:58% 42% 49% 51% / 70% 67% 33% 30%}100%{border-radius:43% 57% 48% 52% / 47% 43% 57% 53%}}@keyframes wobble{0%{border-radius:43% 57% 48% 52% / 47% 43% 57% 53%}16%{border-radius:34% 66% 63% 37% / 63% 46% 54% 37%}33%{border-radius:58% 42% 36% 64% / 52% 70% 30% 48%}50%{border-radius:64% 36% 63% 37% / 68% 46% 54% 32%}67%{border-radius:41% 59% 36% 64% / 52% 33% 67% 48%}83%{border-radius:58% 42% 49% 51% / 70% 67% 33% 30%}100%{border-radius:43% 57% 48% 52% / 47% 43% 57% 53%}}@-webkit-keyframes grow{0%{transform:scale(1)}50%{transform:scale(1.7)}100%{transform:scale(1)}}@keyframes grow{0%{transform:scale(1)}50%{transform:scale(1.7)}100%{transform:scale(1)}}

